import React, {useState, useEffect, useMemo} from 'react'
import {Link} from 'react-router-dom'
import axios from '../../lib/axiosCreate'
import {Row, Col, Button, Table, Badge, Pagination} from 'react-bootstrap'
const initData=[]

export default function BoardList() {
  const display = 5; //     
  const [boardList, setBoardList] = useState([]);//  
  const [totalCount, setTotalCount] = useState(0);//   
  const [pageCount, setPageCount] = useState(1) //   
  const [currentPage, setCurrentPage] = useState(1) //   
  const [pageRangeStart, setPageRangeStart] = useState(1) //     1, 6, 11, 16, ...

  useEffect(()=>{
    const fetchBoardList = async()=>{
      //   
      await getBaordTotal();
      //  
      await getBaordList(0); // offset=0
    }
    fetchBoardList(); // useEffect 
  },[]);

  useEffect(()=>{
    let pageCnt = Math.ceil(totalCount/display);
    setPageCount(pageCnt);
  },[totalCount])

  const getPageNavi = useMemo(()=>{
    let navi=[];
    // Math.min(1, 5) => 1 :    ,   5   
    // ,  1 1  2 1,2 . 5   7 1,2,3,4,5 .
    let end = Math.min(pageRangeStart+4, pageCount);
    for(let i=pageRangeStart;i<=end;i++){
      navi.push(<Pagination.Item 
        key={i} 
        active={i===currentPage}
        onClick={()=>{
          pageHandler(i);
        }}
        >{i}</Pagination.Item>)
    }
    if(pageCount>end){
      navi.push(<Pagination.Item 
        key='next'
        onClick={()=>{
          setPageRangeStart(end+1);
          pageHandler(end+1);
        }}
        ></Pagination.Item>);
    }
    // pageRangeStart:1, 6, 11, 16, ...
    if(pageRangeStart>1){
      navi.unshift(<Pagination.Item 
        key='prev'
        onClick={()=>{
          setPageRangeStart(pageRangeStart-5);
          pageHandler(pageRangeStart-5);
        }}
        ></Pagination.Item>);
    }
    return navi;
  },[pageCount, currentPage])

  const pageHandler = (page)=>{
    // alert('page: '+page);
    const offest=(page-1)*display;
    setCurrentPage(page);
    getBaordList(offest);
  }

  const getBaordTotal = ()=>{
    axios.get('/api/boardTotal')
    .then(response=>
      setTotalCount(response.data.totalCount)
    )
    .catch(err=>{
      alert('Error: '+err.message);
    })
  }
  
  const getBaordList = async(offset)=>{
    if(!offset) offset=0; // offset     
    // offset  DB   
      await axios.get(`/api/boards?offset=${offset}`)
      .then(response=>{
        const responseData = response.data;
        // console.log(JSON.stringify(responseData));
        setBoardList(responseData);
      })
      .catch(err=>{
        alert('Error: '+err.message);
      });
  }

  return (
    <>
      <Row className='my-3'>
        <Col className='p-3 mx-auto' md={10}>
          <h1 className='text-center'>Board List</h1>
        </Col>
      </Row>
      <Row className='my-3'>
        <Col className='p-3 mx-auto text-center' md={10}>
          <h4>   : {totalCount}</h4>
        </Col>
      </Row>
      {boardList.length>0 &&
      <Row className='my-3'>
        <Col className='p-3 mx-auto' md={10}>
          <Table striped border hover>
            <thead>
              <tr>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              { boardList.map((board, i)=>(
              <tr key={board.id}>
                <td>{board.id}</td>
                <td><Link 
                to={`/post/${board.id}`}
                style={{textDecoration: 'none'}}>
                  {board.title}</Link>
                  {board.replyCnt>0&& <Badge bg='warning' className='m-1'>{board.replyCnt}</Badge>}
                </td>
                <td>{board.userid}</td>
                <td>{board.wdate}</td>
                <td>{board.readnum}</td>
              </tr>
              ))
              }
            </tbody>
          </Table>
        </Col>
      </Row>
      }
      {boardList.length===0 &&
      <Row className='my-3'>
        <Col className='p-3 mx-auto' md={10}>
          <h4> .</h4>
        </Col>
      </Row>
      }
      {/*   */}
      <div style={{display: 'flex', justifyContent: 'center', marginTop: 20}}>
        <Pagination>{getPageNavi}</Pagination>
      </div>
    </>
  )
}
