import React, {useState, useRef, useCallback} from 'react'
import { dummyData } from './data'
import TodoHeader from './TodoHeader'
import TodoFrom from './TodoForm'
import TodoList from './TodoList'

export default function TodoApp() {
  const [todos, setTodos] = useState(dummyData); // todos : \
  const idRef = useRef(dummyData.length+1);
  // console.log(idRef.current);
  
  const onCreate=useCallback((content)=>{ //  useCallback   ( )
    let newItem={
      ... todos,
      id: idRef.current,
      content: content,
      wdate: new Date().getTime(),
      isDone: false
    }
    // setTodos([...todos, newItem]);
    setTodos([newItem, ...todos]);
    // idRef.current  id  
    idRef.current++; // 1 
  },[idRef.current, todos]) // idRef, todos     

  const onDelete = useCallback((delId)=>{
    // filter 
    let tmpArr = todos.filter((todo) => (todo.id !== delId));
    setTodos(tmpArr);
  },[todos])

  const onChangeDone = (editId)=>{
    // alert(editId);
    let tmpArr = todos.map((todo)=>(
      // 
      //   isDone  
      todo.id === editId ? {...todo, isDone: !todo.isDone} : todo
    )
  )
  setTodos(tmpArr)
}

  return (
    <div className='container text-center py-4'>
      <h1>Todo App</h1>
      <hr />
      <TodoHeader />
      <TodoFrom onCreate={onCreate} />
      <TodoList todos={todos} onDelete={onDelete} onChangeDone={onChangeDone} />
      {/* todos props TodoList   */}
    </div>
  )
}