import React,{useState} from 'react'
import { Modal, Form, Button } from 'react-bootstrap';

export default function ReplyEditForm(props) {
  const {showEditModal, editReply, setShowEditModal, updateReply, onEditInputChange} = props; // props  

  return (
    <div>
      <Modal show={showEditModal} onHide={()=>{setShowEditModal(false)}}>
        <Modal.Header closeButton>
          <Modal.Title> </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form onSubmit={updateReply}>
            <Form.Group className='mb-3'>
              <Form.Label></Form.Label>
              <Form.Control type='text'
              readOnly
              // editReply  userid   ''  
              value={editReply?.userid}
              name='userid' required />
            </Form.Group>
            <Form.Group className='mb-3'>
              <Form.Label> </Form.Label>
              <Form.Control as="textarea"
              onChange={onEditInputChange} 
              value={editReply?.content}
              name='content' 
              rows="3" 
              required></Form.Control>
            </Form.Group>
            <div style={{textAlign: 'end'}}>
            <Button type='submit'> </Button>
            </div>
          </Form>
        </Modal.Body>
      </Modal>
    </div>
  )
}
