import React,{useState, useEffect} from 'react'
import { useParams, Link, useNavigate } from 'react-router-dom'
import {Button, Col, Row, Card, Container, Badge} from 'react-bootstrap';
import {AiFillHeart, AiFillDislike} from 'react-icons/ai';
import axios from '../../lib/axiosCreate'
import ReplyForm from './ReplyForm';
import ReplyList from './ReplyList';
import ReplyEditForm from './ReplyEditForm';

export default function BoardView() {
  const {id} = useParams(); // url    ( )
  const [board, setBoard] = useState({});//  
  const [logId, setLogId] = useState(''); //   
  const [replies, setReplies] = useState([]); //  
  const navigate = useNavigate();

  //   state
  const [showEditModal, setShowEditModal] = useState(false); //    /
  const [editReply, setEditReply] = useState({}); //   

  useEffect(() => {
    // async useEffect  
    //   async    
    const fetchData=async()=>{
      //   ..   >  
      //
      await updateReadnum();
      // 
      await getBoard();
      // 
      await getReplies();
    };
    fetchData();
  },[id]);

  let uid=null; //   userid   
  useEffect(() => {
    //    userInfo  
    let str=sessionStorage.getItem('userInfo');
    // alert(str, typeof str);
    if(str!==null){
      const user = JSON.parse(str); // parse > stringfy 
      uid = user.userid; // uid    
      setLogId(uid);
    }
  },[]);

  const onDelete = async()=>{
    let yn = window.confirm(` ${id} ?`);
    if(yn){
      await axios.delete(`/api/boards/${id}`)

      navigate('/post')
    }
  };

  const updateReadnum = async () => {
    await axios.put(`/api/boardReadnum/${id}`)
    .then(res=>{
      if(res.data.result==='success'){}
    })
    .catch(err=>console.log('Error :', err.message))
  };

  const getBoard = async () => {
    await axios.get(`/api/boards/${id}`)
    .then(res=>{
      // console.log(JSON.stringify(res.data));
      setBoard(res.data[0]);
    })
    .catch(err=>alert('Error :', err.message))
  };

  //  
  const addReply = async(newReply)=>{
    try{
      const response = await axios.post(`api/boards/${id}/reply`, newReply);
      if(response.data.result==='success'){
        //    
        // alert(' .');
        getReplies();
      }
    }catch(err){
      alert('Error :', err.response.status);
    }
  }
  //  
  const getReplies = async()=>{
    try{
      const response = await axios.get(`api/boards/${id}/reply`);
      setReplies(response.data);
    }catch(err){
      alert('Error :', err.response.status);
    }
  }

  const deleteReply = async(rid)=>{
    // alert(rid); // reply id
    const response = await axios.delete(`api/boards/reply/${rid}`);
    if(response.data.result==='success'){
      getReplies();
    }else{
      alert('  .');
    }
  }

  const startEditReply = (reply)=>{
    setEditReply(reply);
    setShowEditModal(true);
  }

  // editForm input onChange    
  const onEditInputChange = (e) => {
    setEditReply({...editReply, [e.target.name]: e.target.value});
  }
  const updateReply = async(e)=>{
    e.preventDefault();
    // alert(editReply.rid);

    try{
      // editReply body 
      const response = await axios.put(`api/boards/${id}/reply/${editReply.rid}`, editReply);
      if(response.data.result==='success'){
        setShowEditModal(false); //  
        getReplies(); //    
        setEditReply(null); //    
      }else{
        alert('  .');
      }
    }catch(err){
      alert(`Error:  ${err.response.status}`)
    }}

  return (
    <Container className='py-3'>
      <h2>BoardView [ No. {id}]</h2>
      {board&&
      <>
      {/*   (uid)  (board.userid)   ,     */}
        <div className="text-end my-2 mt-5 mb-2">
        {(logId==board.userid)&&
        <>
          <Link to={`/postEdit/${id}`} className='mx-1'><Button variant="success"></Button></Link>
          <Button 
          className='mx-1'
          onClick={onDelete}
          variant='danger'></Button>
        </>
        }
        <Link to={`/post`} className='mx-1'><Button variant="dark"></Button></Link>
        </div>
          <Card>
          <Card.Body>
            <h4>{board.title}</h4>
            <hr />
            <div className="cArea">
              {board.content}
              <br />
              <AiFillHeart style={{color:'red'}} />
              <AiFillDislike style={{color:'green'}} />
              &nbsp;&nbsp;
              <Badge bg="primary">{board.readnum}</Badge>
            </div>
            </Card.Body>
          <Card.Footer>
            Create on {board.wdate} by {board.userid}
          </Card.Footer>
        </Card>
        </>
      }
      {
        !board&& <h4 className='text-center my-5'>  .</h4>
      }
      {
      <Row className='my-4'>
        <Col className='px-3'>
          <h3 className='mt-1'> </h3>
          <ReplyList 
          logId={logId}
          replise={replies} 
          deleteReply={deleteReply} 
          startEditReply={startEditReply} />
        </Col>
      </Row>
      }
      <Row className='my-4'>
        <Col className='px-3'>
          <h3 className='mt-1'> </h3>
          <ReplyForm addReply={addReply} logId={logId} />
        </Col>
      </Row>

      {/*   modal */}
      <Row className='my-4'>
        <Col className='px-3'>
          <ReplyEditForm showEditModal={showEditModal}
          editReply={editReply}
          setShowEditModal={setShowEditModal}
          setEditReply={setEditReply}
          updateReply={updateReply}
          onEditInputChange={onEditInputChange}
           />
        </Col>
      </Row>
    </Container>
  )
}
