import React, {useEffect, useState} from 'react'
import { useParams } from 'react-router-dom';
// useParams : url     
// ex) /users/:id    :id    .
// userParams        .
import { userData } from '../data/userData';

export default function UserDetail() {

  const {id} = useParams();
  const [user, setUser] = useState({id:0, name:'', userId:'', addr:''});

  // useEffect(callback, [ ])
  //  /update/...   
  useEffect(()=>{
    // userData  id    
    // setUser(findUser) user  
    let findUser = {};

    // findUser = userData.find((user) => user.id === parseInt(id));
    // 
    for(let i=0; i<userData.length; i++) {
      let obj = userData[i];
      if (obj.id === Number(id)) {
        findUser = obj;
        break;
      }else{
        findUser = {id:0, name:'', userId:'', addr:''};
      }
    }
    setUser(findUser);
  }, [id]); //  id useParams()  id

  return (
    <div className='container py-4'>
      <h1 className='p-3'>UserDetail [ {id}  ]</h1>
      {// user.id 0  user.userId 
        user.id!==0&&user.userId? 
        <div className="alert alert-primary my-3">
          <h2>MyPage</h2>
          <h3>Name: {user.name}</h3>
          <h3>User ID: {user.userId}</h3>
          <h3>Address: {user.addr}</h3>
        </div>
        :
        <div className="alert alert-danger">
          <h3>{id}   .</h3>
        </div>
      }
    </div>
  )
}
