import React,{useState, useEffect, useRef} from 'react'
import {Row, Col, Form, Button} from 'react-bootstrap'
import { useParams, useNavigate } from 'react-router-dom'
import axios from '../../lib/axiosCreate'

export default function BoardModify() {
  const [form, setForm] = useState({title:'', userid:'', content:''});

  const refTitle = useRef();
  const refUserid = useRef();
  const refContent = useRef();

  const {id} = useParams(); //   
  const navigate = useNavigate();

  useEffect(() => {
    getBoard();
  },[id]);

  //  /api/boards/11 >> 11  
  const getBoard = async () => {
    const response = await axios.get(`/api/boards/${id}`)
    console.log(response.data);
    if(response.data.length===1){
      setForm(response.data[0]);
    }else{
      alert('   .');
      navigate('/post');
    }
  }

  const {title, userid, content} = form; // form.title, form.userid, form.content
  const onSubmitHandler = (e)=>{
    e.preventDefault();
    if(!title.trim()){
      alert(', ,  .');
      refTitle.current.focus();
      return;
    }else if(!userid.trim()){
      alert(' .');
      refUserid.current.focus();
      return;
    }else if(!content.trim()){
      alert(' .');
      refContent.current.focus();
      return;
    }
    //  PUT /api/boards/11 >> 11  
    requestModify();
  }
  const requestModify = async () => {
    try{
      const response = await axios.put(`/api/boards/${id}`, form) //   axios.put,  
      if(response.data.result==='success'){
        alert(response.data.msg);
        navigate(`/post/${id}`);
      }
    }catch(err){
      alert('Error:'+err.response.status)
    }
  }

  const onChangeHandler = (e)=>{
    // e.target.name : input name
    // e.target.value :   
    setForm({... form, [e.target.name]: e.target.value})
  }

  const onResetHandler = ()=>{
    setForm({...form, title:'', userid:'', content:''})
  }

  return (
    <>
      <Row className="my-3">
        <Col className="p-3 mx-auto" md={8}>
          <h1 className="text-center">Board Modify</h1>
        </Col>
      </Row>
      <Row className="my-3">
        <Col className="p-3 mx-auto" md={8}>
          <Form onSubmit={onSubmitHandler}>
            <Form.Group>
              <Form.Label> </Form.Label>
              <Form.Control
                type="text"
                name="title"
                onChange={onChangeHandler}
                ref={refTitle}
                value={form.title}
                placeholder=" "
              ></Form.Control>
            </Form.Group>
            <Form.Group>
              <Form.Label></Form.Label>
              <Form.Control
                type="text"
                name="userid"
                onChange={onChangeHandler}
                ref={refUserid}
                value={form.userid}
                placeholder=" "
              ></Form.Control>
            </Form.Group>
            <Form.Group>
              <Form.Label> </Form.Label>
              <Form.Control
                as="textarea"
                rows={7}
                name="content"
                onChange={onChangeHandler}
                ref={refContent}
                value={form.content}
                placeholder=" "
              ></Form.Control>
            </Form.Group>
            <div className="my-2 text-center">
              <Button type="submit" variant="success" className="m-2">
                  
              </Button>
              <Button type="reset" onClick={onResetHandler} variant="warning">
                
              </Button>
            </div>
          </Form>
        </Col>
      </Row>
    </>
  )
}
