import React, { useState, useRef } from 'react';
import { Form, Container, Button, ListGroup } from 'react-bootstrap';

export default function UseRefHook() {
  const [name, setName] = useState('');
  const [score, setScore] = useState(''); // 0      
  const [list, setList] = useState([]); //  

  const nameRef = useRef(null); // input   DOM    hook
  const scoreRef = useRef(null);

  //  
  const getAverage = () => {
    if(list.length === 0) return 0; //   0 
    let sum = 0;
    list.forEach((student) => {
      sum += Number(student.score); //   
    });
    let avg = sum / list.length;
    return avg.toFixed(2);
  };

  const handleSubmit = (e) => {
    e.preventDefault(); // submit    .
    if (!name) {
      alert(' .');
      //document.getElementById('name').focus();
      nameRef.current.focus(); //   
      return;
    }
    if (!score) {
      alert(' .');
      scoreRef.current.focus(); //   
      return;
    }
    if (isNaN(score)) {
      alert('  .');
      scoreRef.current.select(); //  
      return;
    }
    console.log('   **************');
    // list    
    //    
    const student = {name, score};
    setList([...list, student]);
    console.log(list);
    setName('');//   
    setScore('');//   
    nameRef.current.focus(); //   
  };

  return (
    <Container>
      <h1>useRef()  </h1>
      <hr />
      <Form>
        <Form.Group>
          <Form.Label>: </Form.Label>
          <Form.Control
            type="text"
            name="name"
            ref={nameRef}
            onChange={(e) => setName(e.target.value)}
            value={name} // input state 
            placeholder=" ."
          />
        </Form.Group>
        <Form.Group>
          <Form.Label> : </Form.Label>
          <Form.Control
            type="text"
            name="score"
            ref={scoreRef}
            onChange={(e) => setScore(e.target.value)}
            value={score} // input state 
            placeholder=" ."
          />
        </Form.Group>
        <Form.Group>
          <br />
          <Button variant="success" onClick={handleSubmit}>
            
          </Button>
          <hr />
        </Form.Group>
        <ListGroup>
          {list.map((student, index) => (
              <ListGroup.Item>
                <div key={index}>
                  <span>{student.name}</span> : <span>{student.score}</span>
                </div>
              </ListGroup.Item>
            )
          )}
        </ListGroup>
        <br />
        <h1 className="text-danger">: {getAverage()}</h1>
      </Form>
    </Container>
  );
}
