import React,{useEffect, useState} from 'react'
import { Button, Form } from 'react-bootstrap';
import axios from '../../lib/axiosCreate';

export default function ReplyForm({addReply, logId}) {
  const [reply, setReply] = useState({userid: '', content: ''});
  //   : POST /api/boards/:id/reply
  //   : GET /api/boards/:id/reply | id    
  //  : GET /api/boards/:id/reply/:rid | id   rid  
  //   : PUT /api/boards/:id/reply/:rid | id   rid  
    const onSubmitHandler = async(e) => {
      e.preventDefault();
  
      // props   addReply 
      await addReply(reply);
      setReply({userid: logId, content: ''});
    }
  
  const onChangeHandler = (e) => {
    setReply({...reply, [e.target.name]: e.target.value});
  }

  useEffect(() => {
    setReply((prevReply) => ({ ...prevReply, userid: logId }));
  }, [logId]);
  
  return (
    <div>
      {logId &&
      <Form onSubmit={onSubmitHandler}>
        <Form.Group className='mb-3'>
          <Form.Label></Form.Label>
          <Form.Control type='text' 
          name='userid' 
          // onChange={onChangeHandler}
          readOnly
          value={logId}
          required></Form.Control>
        </Form.Group>
        <Form.Group className='mb-3'>
          <Form.Label> </Form.Label>
          <Form.Control as="textarea" rows="3" 
          onChange={onChangeHandler}
          value={reply.content}
          name='content' required></Form.Control>
        </Form.Group>
        <Button type='submit'> </Button>
      </Form>
      }
      {!logId&&
        <div className='text-center'>   .</div>
      }
    </div>
  )
}
