import React, {useState, useEffect} from 'react';
import { useSearchParams } from 'react-router-dom';
// useSearchParams : query string   hook (?)
// useParams : path parameter   hook (:)
import { Pagination, Spinner } from 'react-bootstrap';

// query string 
// ajax2?page=1&per_page=3
// https://reqres.in/api/users?page=1&per_page=3

export default function ListUser() {
  const [userList, setUserList] = useState([]);
  const [total, setTotal] = useState(0); //   (12) / 3 = 4pages
  //  = 
  // Math.ceil(  /    ) // Math.ceil() : 
  // Math.ceil(total / per_page)
  const [totalPages, setTotalPages] = useState(1); //   1  
  const [currentPage, setCurrentPage] = useState(1); //   
  const [loading, setLoading] = useState(false); //  

  // query string    (page, per_page) 
  const [params] = useSearchParams();
  const perPage = Number(params.get('per_page'));
  // console.log(params.get('page'));
  // console.log(params.get('per_page'));
  
  const getAllUsers = (page, per_page)=>{

    setLoading(true);

    // fetch()   REST API  :    
    // fetch(`https://reqres.in/api/users?page=${params.get('page')}&per_page=${params.get('per_page')}`)
    let url =`https://reqres.in/api/users?page=${page}&per_page=${per_page}&delay=3`;
    fetch(url)
    .then(res=>res.json())
    .then(usersData=>{
      if(!usersData.data){
        alert(' .');
        return;
      }
      setLoading(false);
      
      // console.log(JSON.stringify(usersData)); // JSON.stringify() : JSON   
      let tmpArr = [... usersData.data]; //       
      let tmpTotal = usersData.total; // total  
      setUserList(tmpArr);
      setTotal(tmpTotal); //   (12)
      setTotalPages(usersData.total_pages); //  (4)
    })
    .catch(err=>{
      console.warn(err);
    });
  }

  useEffect(()=>{
    getAllUsers(1, perPage);
  }, []);

  const pageClickHandler = (page)=>{
    setCurrentPage(page);
    getAllUsers(page, perPage);
  };

  return (
    <div className='container py-4'>
      <h2>All Users - {total}</h2>
      <br />
      <ul>
        {
          loading && 
          <Spinner animation='border' role="status">
            <span className='visually-hidden' >Loading...</span>
          </Spinner>
        }
      {userList&&userList.map((user, i)=>(
        <li key={user.id}>
          <img src={user.avatar} alt="" /><br />
          {user.first_name} {user.last_name} <br />
          <br />
          {user.email}
        </li>
      ))
        }
      </ul>
      <div>
        {/* 
        Array(len)
        Array(total) :   total  
        [... Array(5)] :   5  
        [undefined, undefined, undefined, undefined, undefined]
        [    0    ,     1    ,     2    ,     3    ,     4    ]
        Array.keys() :   (index  )
        */}
        <Pagination> {/* totalPages     */}
          {
            [... Array(totalPages).keys()].map((page)=>(
              <Pagination.Item 
              key={page+1} 
              active={(page+1)===currentPage}
              onClick={()=>{
                pageClickHandler(page+1);}}
              >{page+1}</Pagination.Item>
            ))
          }
        </Pagination>
      </div>
    </div>
  )
}