import React,{useState, useEffect} from 'react';
import {Link} from 'react-router-dom';
import './Header.css'
import { useSigninUser } from '../components/member/SigninUserContext';

export default function Header() {
  const {user, signoutUser, signinAuthUser} = useSigninUser();
  // context  user, signoutUser 
  //  ,      useEffect 
  // context  user ,  
  //  userInfo , context signinauthUser() 
  const [userInfo, setUserInfo] = useState(null);

  useEffect(()=>{
    if(!user){
      const tmpUserInfo = sessionStorage.getItem('userInfo'); //    userInfo 
      if(tmpUserInfo){//  userInfo 
        console.log('tmpUserInfo:'+tmpUserInfo, typeof tmpUserInfo); // string
        // json  string json    : JSON.parse()==>json
        setUserInfo(JSON.parse(tmpUserInfo));
        signinAuthUser(JSON.parse(tmpUserInfo));
      }
    }
  },[]);

  const onSingout = () => {
    signoutUser();//   Provider  user null 
    sessionStorage.clear();
    sessionStorage.removeItem('userInfo');
    setUserInfo(null);
    window.location.href='/'; //  
  }

  return <div className="header">
    <ul>
      <li>
      {/* 
       a X
      - a           (reload).
      - react router    ,
           react          .
      */}
        <Link to="/">Home</Link>
      </li>
      {!user&&
        <>
          <li>
            <Link to="/signup">SignUp</Link>
          </li>
          <li>
            <Link to="/signin">SignIn</Link>
          </li>
        </>
      }
      {user&&
        <>
          <li className='bg-warning'>
            <Link to="#">{user.userid}  </Link>
          </li>
          <li>
            <Link to="#" onClick={onSingout}>SignOut</Link>
          </li>
        </>
      }
      <li>
        <Link to="/comp1">MyComp1</Link>
      </li>
      <li>
        <Link to="/members">Members</Link>
      </li>
      <li>
        <Link to="/post">Post (Board)</Link>
      </li>
    </ul>
  </div>;
}
