import React, { useCallback, useState, useRef } from 'react'
import {Form, Row, Col, Button} from 'react-bootstrap'
import { BsFillPencilFill } from "react-icons/bs";
import { AiOutlineReload } from "react-icons/ai";
import { AiOutlinePlus } from "react-icons/ai";
// import './TodoForm.css'


export default function TodoFrom({onCreate}) {
  const [content, setContent] = useState('');
  // const [isComposing, setIsComposing] = useState(false);
  const contentRef = useRef();


  const onChange = useCallback((e)=>{
    setContent(e.target.value);
  }, [])

  const onKeyDown=(e)=>{
    console.log();
    if(e.keyCode===13){//  
       onSubmit();
    }
 }

  const onSubmit=()=>{
    // alert('+')
    //  
    if(!content){
      alert('  .');
      contentRef.current.focus();
      return;
    }
    //  props  ( )  content  
    console.log(content);
    onCreate(content); //    onCreate  
    setContent('');
  }
  const onReset=()=>{
    // alert('reset')
    setContent('');
    contentRef.current.focus(); // current input  

  }

  return (
    <div className='my-5 text-start container'>
    <div className='todo_add'><h3>    </h3><BsFillPencilFill className='mx-2' /></div>
      <Row>
        <Col xs={12} sm={8} md={8}>
            <input className='inputCss' 
            ref={contentRef}
            value={content}
            onChange={onChange}

            onKeyDown={onKeyDown}

            // onKeyDown={(e) => {
            //   if (e.key === 'Enter' && !isComposing) {
            //     onSubmit();
            //   }
            // }}

            // onCompositionStart:  IME      .
            // onCompositionUpdate:  IME      .
            // onCompositionEnd:  IME      .
            // onCompositionStart={() => setIsComposing(true)}
            // onCompositionEnd={() => setIsComposing(false)}

            name='content' 
            type="text" 
            placeholder='  .'/>
        </Col>


        <Col style={{whiteSpace: 'nowrap'}} xs={12} sm={4} md={4}>
          <Button 
          onClick={onSubmit}
          variant='outline-danger' 
          className='mx-2'><AiOutlinePlus /></Button>
          <Button 
          onClick={onReset}
          variant='outline-success'><AiOutlineReload /></Button>
        </Col>
      </Row>
    </div>
  )
}