import React, { useState, useRef, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Row, Col, Form, Button } from 'react-bootstrap';
import axios from '../../lib/axiosCreate';

export default function BoardForm({onMode}) {

  const initData = {
    title: '',
    userid: '',
    content: '',
  };

  const [form, setForm] = useState(initData);

  const refTitle = useRef();
  const refUserid = useRef();
  const refContent = useRef();

  const navigate = useNavigate();

  let uid=null; //   userid   
  useEffect(() => {
    //    userInfo  
    let str=sessionStorage.getItem('userInfo');
    // alert(str, typeof str);
    if(str!==null){
      const user = JSON.parse(str); // parse > stringfy 
      uid=user.userid; // uid    
      setForm({...form, userid:uid}); //    form.userid 
    }else{
      alert(' .');
      navigate('/signin');
    }
  },[]);

  const onChangeHandler = (e) => {
    // []:
    const frmdata = { ...form, [e.target.name]: e.target.value };
    // console.log(frmdata);
    setForm(frmdata);
  };

  const onSubmitHandler = (e) => {
    // axios  ,form submit     .
    e.preventDefault();
    const {userid, title, content} = form;
    if(!title.trim()){
      alert(' ');
      refTitle.current.focus();
      return;
    }
    if(!userid.trim()){
      alert(' ');
      refUserid.current.focus();
      return;
    }
    if(!content.trim()){
      alert(' ');
      refContent.current.focus();
      return;
    }
    requestBoardWrite();
  };

  const requestBoardWrite=()=>{
    axios.post('/api/boards', form)
    .then(response=>{
      // console.log(JSON.stringify(response.data));
      if(response.data.result==='success'){
        onMode('list');
        alert(response.data.msg);
      }else{
        alert('  ');
        onResetHandler();
      }
    })
    .catch(err=>{
      console.warn(err);
    })
  };

  const onResetHandler = () => {
    setForm({...form, ...initData});
    refTitle.current.focus();
  };

  return (
    <>
      <Row className="my-3">
        <Col className="p-3 mx-auto" md={8}>
          <h1 className="text-center">Board Write</h1>
        </Col>
      </Row>
      <Row className="my-3">
        <Col className="p-3 mx-auto" md={8}>
          <Form onSubmit={onSubmitHandler}>
            <Form.Group>
              <Form.Label> </Form.Label>
              <Form.Control
                type="text"
                name="title"
                onChange={onChangeHandler}
                ref={refTitle}
                value={form.title}
                placeholder=" "
              ></Form.Control>
            </Form.Group>
            <Form.Group>
              <Form.Label></Form.Label>
              <Form.Control
                type="text"
                name="userid"
                onChange={onChangeHandler} readOnly
                ref={refUserid}
                value={form.userid}
                placeholder=" "
              ></Form.Control>
            </Form.Group>
            <Form.Group>
              <Form.Label> </Form.Label>
              <Form.Control
                as="textarea"
                rows={7}
                name="content"
                onChange={onChangeHandler}
                ref={refContent}
                value={form.content}
                placeholder=" "
              ></Form.Control>
            </Form.Group>
            <div className="my-2 text-center">
              <Button type="submit" variant="success" className="m-2">
                  
              </Button>
              <Button type="reset" onClick={onResetHandler} variant="warning">
                
              </Button>
            </div>
          </Form>
        </Col>
      </Row>
    </>
  );
}
